.annotation {
  padding-left: 0.2rem;
  background-color: rgba(65, 66, 136, 0.05);
  cursor: help;
}
.annotation .annotation_bracket {
  color: #414288;
}
.annotation sup {
  font-size: 0.75em;
}

.annotation_hover {
  background-color: rgba(65, 66, 136, 0.2);
}

.annotations {
  width: 100%;
}
.annotations ul {
  list-style-type: none;
  margin-left: -2rem;
}
@media (min-width: 768px) {
  .annotations ul {
    margin-left: 0;
  }
}
.annotations li {
  display: flex;
  margin-top: 0.7rem;
}
.annotations .annotation_number {
  color: #414288;
  font-size: 1.1rem;
  flex-grow: 0;
  flex-shrink: 0;
  width: 1rem;
  margin-right: 1rem;
}

.annotation_preview {
  position: fixed;
  bottom: 2rem;
  right: 1rem;
  font-size: 0.9rem;
  display: none;
  width: 100%;
  min-width: 10rem;
  max-width: calc((100vw - 700px) / 2 - 2.5rem);
  height: fit-content;
  border: solid 4px #414288;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 1em;
  background-color: white;
  visibility: hidden;
}
@media (min-width: 768px) {
  .annotation_preview {
    visibility: visible;
  }
}

.annotation_preview_number {
  color: #414288;
}

.annotation_preview_content {
  margin-top: 0.5rem;
}

a {
